.block-icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &__icon {
    width: 100%;
    height: 100%;
    border-radius: 15px;
    background-color: $white;
    padding: $gap-200;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: $gap-100;
    &:last-child {
      margin: 0; }
    &--full {
      padding: 0;
      background-color: transparent; }
    &--empty {
      box-shadow: none;
      padding: 0;
      border-radius: 0;
      background-color: transparent; }
    &--blue {
      background-color: $blue-500; }
    &--shadow {
      box-shadow: $shadow-1200; } }
  &__title {
    @include rm;
    font-size: rem(15);
    line-height: 1.25;
    color: $text-black;
    text-align: center;
    max-width: 155px; } }
