.everybody {
  &__container {
    display: flex;
    align-items: stretch;
    @include r(767) {
      flex-direction: column; } }
  &__content {
    max-width: 500px;
    width: 100%;
    flex-shrink: 0;
    align-self: center;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    padding-right: $gap-900;
    position: relative;
    z-index: $z-index-1;
    @include r(1023) {
      width: 400px;
      padding-right: $gap-600; }
    @include r(767) {
      width: 100%;
      margin-bottom: $gap-300; }
    @include r(479) {
      padding: 0; } }
  &__media {
    flex-grow: 1;
    position: relative;
    padding: $gap-300 0;
    display: flex;
    justify-content: center;
    align-items: center;
    @include r(767) {
      padding: 0; } }
  &__media-tile {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-40%, -50%);
    z-index: $z-index-0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @include r(1023) {
      transform: translate(-50%, -50%); }
    @include r(767) {
      position: relative;
      top: 0;
      left: 0;
      transform: translate(0); }
    .block-icon {
      width: 125px;
      height: 125px;
      @include r(1365) {
        width: 100px;
        height: 100px; }
      @include r(1023) {
        width: 75px;
        height: 75px; }
      @include r(767) {
        width: 65px;
        height: 65px; } } }

  &__media-tile-row {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    gap: 25px;
    margin-bottom: $gap-250;
    @include r(1023) {
      gap: $gap-150;
      margin-bottom: $gap-150; }
    &:nth-child(1) {
      padding-left: 75px; }
    &:nth-child(2) {
      padding-left: 150px;
      @include r(1023) {
        padding-left: 0; } }
    &:nth-child(3) {
      padding-left: 50px; }
    &:last-child {
      margin-bottom: 0;
      @include r(767) {
        display: none; } } }
  &__media-image {
    position: relative;
    z-index: $z-index-1;
    height: 100%;
    max-height: 730px;
    @include r(767) {
      display: none; } }
  &__title {
    margin-bottom: $gap-200; } }

