.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: $z-index-10;
  &__wrapper {
    padding: $gap-500 0 $gap-200;
    @include r(1023) {
      padding: $gap-100;
      background-color: $white; } }
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center; }

  &__icon {
    img {
      max-width: 165px;
      height: 44px;
      @include r(1023) {
        max-width: 130px;
        height: 35px; }
      @include r(439) {
        height: 23px;
        max-width: 103px; } }
    &--modal {
      display: none;
      @include r(1023) {
        display: block;
        margin: 0 auto 0 0; } } }

  &__navigation {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-shrink: 0; }
  &__navigation-social {
    margin-right: $gap-250;
    @include r(1023) {
      display: none; } }
  &__navigation-burder {
    flex-shrink: 0; }

  &__modal {
    position: fixed;
    top: 0;
    right: 0;
    width: 560px;
    height: 100vh;
    pointer-events: none;
    opacity: 0;
    transition: opacity $transition-100 0.25s;
    @include r(767) {
      width: 100%; }
    &::before {
      content: "";
      position: fixed;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100vh;
      background-color: rgba($white, $opacity-50);
      backdrop-filter: blur(2px); }
    &--opened {
      transition: opacity $transition-100;
      pointer-events: auto;
      opacity: 1;
      .header__modal-container {
        opacity: 1;
        transition: opacity $transition-100 0.25s; } } }

  &__modal-container {
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: $gap-500 $gap-600;
    background: $gradient-200;
    opacity: 0;
    transition: opacity $transition-100;
    overflow: auto;
    @include r(1023) {
      justify-content: flex-start; }
    @include r(767) {
      padding: $gap-300; } }

  &__modal-container-head {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    @include r(1023) {
      padding-bottom: $gap-500; }
    @include r(767) {
      padding-bottom: $gap-200; } }

  &__modal-navigation {
    padding: 0 0 $gap-500;
    margin: auto 0;
    @include r(1023) {
      margin: 0; }
    &:hover {
      .header__modal-navigation-item {
        color: $blue-1000;
        .header__modal-navigation-item-link {
          &::after {
            opacity: 0; } }
        &:hover {
          color: $white;
          .header__modal-navigation-item-link {
            &::after {
              opacity: 1; } } } } } }
  &__modal-navigation-item {
    color: $white;
    font-size: rem(54);
    @include rb;
    line-height: 1.15;
    transition: color $transition-200;
    @include r(1023) {
      font-size: rem(34); }
    &--active {
      color: $white;
      .header__modal-navigation-item-link {
        &::after {
          opacity: 1; } } } }
  &__modal-navigation-item-link {
    padding: $gap-200 0;
    display: inline-block;
    position: relative;
    @include r(767) {
      padding: $gap-150 0; }
    &::after {
      content: "";
      position: absolute;
      bottom: $gap-150;
      left: 0;
      width: 100%;
      height: 4px;
      background-color: $white;
      opacity: 0;
      transition: opacity $transition-200;
      @include r(767) {
        bottom: $gap-100; } }
    &--active {
      color: $white;
      &::after {
        opacity: 1; } } }

  &__modal-social {
    width: 100%;
    justify-content: center;
    @include r(1023) {
      margin: auto 0 0; } } }
