.block-phone-container {
  width: 250px;
  border-radius: 30px;
  box-shadow: $shadow-1100;
  border: $gray-100 solid 8px;
  overflow: hidden;
  &--small {
    width: 250px; }
  &--medium {
    width: 350px;
    @include r(1365) {
      width: 300px; } }
  @include r(1023) {
    width: 230px; }
  img {
    width: 100%; } }
