.hero {
  &__container {
    padding: $gap-700 0 $gap-400;
    display: grid;
    grid-template-columns: minmax(0, 1fr);
    grid-template-rows: minmax(5%, 50px) minmax(2em, 20%) 60% minmax(15%, 100px);
    justify-items: center;
    max-width: 610px;
    width: 100%;
    margin: 0 auto;
    @include r(1023) {
      padding: $gap-800 0 $gap-200;
      grid-template-rows: repeat(3, auto); } }
  &__logo {
    margin-bottom: $gap-100;
    @include r(1023) {
      display: none; } }
  &__block-title {
    text-align: center;
    margin-bottom: $gap-500;
    @include r(1023) {
      margin-bottom: $gap-150; } }
  &__illustration {
    width: 100%;
    margin: 0;
    position: relative;
    display: flex;
    justify-content: center; }
  &__illustration-image {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    height: 100%;
    max-height: 550px;
    z-index: $z-index-1;
    @include r(1023) {
      max-height: 480px;
      position: relative;
      top: 0;
      left: 0;
      transform: translate(0); } }
  &__illustration-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transition: transform $transition-100;
    &.icon-1 {
      transform: translate(calc(-100% - 450px), calc(-50% - 125px));
      .block-icon__icon {
        width: 100px;
        height: 100px; } }
    &.icon-2 {
      transform: translate(calc(-100% - 230px), -50%);
      .block-icon__icon {
        width: 100px;
        height: 100px;
        border-radius: $gap-300; } }
    &.icon-3 {
      transform: translate(calc(-100% - 320px), calc(50% + 75px));
      .block-icon__icon {
        width: 80px;
        height: 80px; } }
    &.icon-4 {
      transform: translate(calc(100% + 230px), calc(-50% - 100px));
      .block-icon__icon {
        width: 95px;
        height: 95px; } }
    &.icon-5 {
      transform: translate(calc(100% + 75px), 0%);
      .block-icon__icon {
        padding: $gap-100;
        width: 70px;
        height: 70px; } }
    &.icon-6 {
      transform: translate(calc(100% + 350px), 100%);
      .block-icon__icon {
        padding: $gap-150;
        width: 90px;
        height: 90px; } }
    &.icon-7 {
      transform: translate(calc(-100% - 100px), -100%);
      .block-icon__icon {
        padding: $gap-150;
        width: 85px;
        height: 85px; } }
    &.icon-8 {
      transform: translate(calc(-100% - 650px), -75%);
      .block-icon__icon {
        padding: $gap-100;
        width: 75px;
        height: 75px; } }
    &.icon-9 {
      transform: translate(calc(-100% - 500px), 100%);
      .block-icon__icon {
        padding: $gap-150;
        width: 60px;
        height: 60px; } }
    &.icon-10 {
      transform: translate(calc(-100% - 750px), calc(100% + 100px)); }
    &.icon-11 {
      transform: translate(calc(-100% - 50px), calc(-100% - 165px));
      z-index: $z-index-2;
      .block-icon {
        width: 200px;
        height: 70px; }
      .block-icon__icon {
        padding: 0;
        background-color: transparent;
        box-shadow: none; } }
    &.icon-12 {
      transform: translate(calc(100% + 20px), calc(-100% - 185px));
      z-index: $z-index-2;
      .block-icon__icon {
        padding: $gap-150;
        width: 75px;
        height: 75px; } }
    &.icon-13 {
      transform: translate(calc(100% + 80px), calc(-100% - 165px));
      z-index: $z-index-1;
      .block-icon__icon {
        padding: $gap-150;
        width: 70px;
        height: 70px; } }
    &.icon-14 {
      transform: translate(calc(65% + 600px), -50%);
      .block-icon__icon {
        padding: $gap-150;
        width: 95px;
        height: 95px; } }
    &.icon-15 {
      transform: translate(calc(65% + 750px), calc(100% + 100px));
      .block-icon__icon {
        padding: $gap-150;
        width: 100px;
        height: 100px; } }
    &.icon-16 {
      transform: translate(25%, calc(-100% - 75px));
      z-index: $z-index-2;
      .block-icon {
        width: 200px;
        height: 70px; }
      .block-icon__icon {
        padding: 0;
        background-color: transparent;
        box-shadow: none; } } }
  &__illustration-chat {
    padding: $gap-100 $gap-200;
    background-color: $white;
    border-radius: $gap-200;
    display: flex;
    width: max-content;
    position: relative;
    img {
      width: 35px;
      height: 35px;
      margin-right: $gap-200;
      &:last-child {
        margin: 0; } } }
  &__illustration-chat-text {
    @include rb;
    font-size: rem(17);
    padding: $gap-100 $gap-400;
    position: relative; }
  &__illustration-chat-text-icon {
    position: absolute;
    bottom: 90%;
    right: 0; }
  &__social {
    display: flex;
    align-items: flex-end;
    padding-top: $gap-350;
    @include r(1023) {
      grid-row: 2 / 3;
      align-items: flex-start;
      padding-top: 0;
      margin-bottom: $gap-500; } } }
