.be-iconist {
  &__container {
    padding: $gap-700 0 $gap-350;
    display: grid;
    grid-template-columns: minmax(0, 1fr);
    grid-template-rows: minmax(50px, 5%) minmax(2em, 19%) 65% minmax(10%, 80px);
    justify-items: center;
    @include r(1365) {
      height: auto;
      grid-template-rows: auto auto 550px auto; }
    @include r(1023) {
      grid-template-rows: auto auto 450px auto; }
    @include r(767) {
      grid-template-rows: auto 450px auto; } }
  &__logo {
    margin-bottom: 5px;
    @include r(1023) {
      margin-bottom: $gap-200; }
    @include r(767) {
      display: none; } }
  &__block-title {
    text-align: center;
    margin: 0 auto $gap-500;
    max-width: 800px;
    @include r(1023) {
      margin-bottom: $gap-250; } }
  &__illustration {
    position: relative;
    width: 100%;
    @include r(1365) {
      height: 550px; }
    @include r(1023) {
      height: 450px; } }
  &__illustration-main {
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: stretch;
    justify-content: center;
    picture,
    img {
      height: 100%; }
    img {
      object-fit: cover; }
    .block-phone-container {
      z-index: $z-index-1; } }
  &__tile {
    position: absolute;
    z-index: $z-index-0;
    width: calc( 6 * 95px + 5 * 45px + 100px);
    height: 100%;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    overflow: hidden;
    padding-bottom: $gap-400;
    @include r(767);
    &.tile-1 {
      right: calc(50% + 50px);
      align-items: flex-start;
      @include r(1365) {
        align-items: center; }
      @include r(767) {
        align-items: flex-end; } }
    &.tile-2 {
      left: calc(50% + 50px);
      align-items: flex-end;
      @include r(1365) {
        align-items: center; }
      @include r(767) {
        align-items: flex-start; }
      .be-iconist__tile-row {
        justify-content: flex-end;
        direction: rtl; } }
    .block-icon {
      transition: transform $transition-200; }
    .block-icon__icon {
      width: 95px;
      height: 95px;
      padding: $gap-150;
      @include r(767) {
        width: 75px;
        height: 75px; } } }
  &__tile-row {
    display: grid;
    grid-template-columns: repeat( 6, 95px );
    gap: $gap-450;
    margin-bottom: $gap-450;
    @include r(1365) {
      gap: $gap-350;
      margin-bottom: $gap-350; }
    @include r(767) {
      gap: $gap-100;
      margin-bottom: $gap-250; }
    &:nth-child(1) {
      .block-icon__icon {
        transform: rotate(-20deg); } }
    &:nth-child(2) {
      margin-left: - $gap-250;
      .block-icon__icon {
        transform: rotate(20deg); } }
    &:nth-child(3) {
      .block-icon__icon {
        transform: rotate(-20deg); } }
    &:last-child {
      margin-bottom: 0; } }
  &__social {
    padding-top: $gap-250;
    @include r(1365) {
      padding-top: $gap-500; } } }
