$burger-item-spacer: 4px;

.burger {
  $self: &;
  border: 0;
  width: 65px;
  height: 65px;
  border-radius: 50%;
  background: $gradient-100;
  box-shadow: $shadow-200;
  position: relative;
  cursor: pointer;
  transition: box-shadow $transition-100;
  padding: $gap-200;
  display: block;
  @include r(1023) {
    width: 45px;
    height: 45px; }
  @include r(767) {
    width: 40px;
    height: 40px; }
  &:hover {
    box-shadow: $shadow-500; }
  &:focus {
    outline: 0; }

  &__item {
    width: 20px;
    height: 3px;
    background-color: $white;
    border-radius: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    &:nth-of-type(1) {
      transform: translate(-50%, calc(-50% + #{$burger-item-spacer})); }
    &:nth-of-type(2) {
      transform: translate(-50%, calc(-50% - #{$burger-item-spacer})); }
    @include r(1023) {
      width: 13px;
      height: 2px;
      &:nth-of-type(1) {
        transform: translate(-50%, calc(-50% + 3px)); }
      &:nth-of-type(2) {
        transform: translate(-50%, calc(-50% - 3px)); } } }

  &__text {
    top: 50%;
    right: 100%;
    position: absolute;
    transform: translateY(-50%);
    padding-right: $gap-250;
    @include rb;
    font-size: rem(25);
    opacity: 0;
    transition: opacity $transition-100;
    color: $white;
    @include r(1023) {
      display: none; } }

  &--opened {
    background: $white;
    #{$self}__item {
      background-color: $blue-500;
      width: 24px;
      height: 3.5px;
      &:nth-of-type(1) {
        transform: translate(-50%, -50%) rotate(45deg); }
      &:nth-of-type(2) {
        transform: translate(-50%, -50%) rotate(-45deg); }
      @include r(1023) {
        width: 15px;
        height: 2px; } }
    #{$self}__text {
      opacity: 1; } } }
