.features {
  &__container {
    padding: 130px 0 $gap-400;
    height: 100vh;
    display: grid;
    grid-template-columns: minmax(0, 1fr);
    grid-template-rows: 1fr auto;
    @include r(1023) {
      height: auto;
      padding: $gap-500 0;
      grid-template-rows: auto auto; } }
  &__content {
    width: 100%;
    height: 100%;
    position: relative; }
  &__slider {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    @include r(1023) {
      position: relative; } }
  &__slider-slide {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: stretch;
    flex-shrink: 0;
    @include r(1023) {
      flex-direction: column; } }
  &__slider-slide-content {
    max-width: 600px;
    align-self: center;
    flex-shrink: 0;
    padding-right: $gap-250;
    @include r(1279) {
      max-width: 450px;
      padding: 0 $gap-500 0 0;
      margin-bottom: $gap-300; }
    @include r(1199) {
      max-width: 400px; }
    @include r(1023) {
      max-width: 100%;
      padding-right: 0; }
    @include r(767) {
      width: 100%;
      margin-bottom: 0; }
    .block-title {
      margin-bottom: $gap-300; }
    .block-subtitle {
      @include r(1023) {
        display: none; } } }
  &__slider-slide-content-icon {
    width: 150px;
    height: 150px;
    margin-bottom: $gap-300;
    @include r(1023) {
      display: none; } }
  &__slider-slide-media {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    @include r(479) {
      justify-content: space-around; } }
  &__slider-slide-media-image {
    width: 320px;
    height: 100%;
    position: relative;
    margin: 0 $gap-200;
    @include r(1365) {
      width: 250px; }
    @include r(1023) {
      width: 200px; }
    @include r(767) {
      width: 150px;
      margin: 0 $gap-100 0 0;
      &:last-child {
        margin: 0; } }
    .block-phone-container {
      width: 100%;
      height: 100%;
      position: relative;
      z-index: $z-index-1; }
    picture,
    img {
      @include r(767) {
        height: 100%;
        object-fit: cover; } } }
  &__slider-slide-media-bg {
    &.icon-1,
    &.icon-2,
    &.icon-3 {
      position: absolute;
      z-index: $z-index-0; }
    &.icon-1 {
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: 100% 50%; }
    &.icon-2 {
      top: 0;
      left: 50%;
      transform: translate(-50%, -50%); }
    &.icon-3 {
      top: 50%;
      right: 0;
      transform: translate(0, -50%); }
    &.icon-3 {} }
  &__navigation {
    padding-top: $gap-600;
    grid-row: 2 / 3;
    @include r(1023) {
      grid-row: 1 / 2;
      padding: 0; } }
  &__navigation-list {
    display: flex;
    justify-content: space-between;
    position: relative;
    @include r(1023) {
      margin-bottom: 35px; }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 4px;
      background-color: rgba($blue-500, $opacity-10); } }
  &__navigation-list-item {
    padding-top: 20px;
    position: relative;
    width: calc(100% / 8);
    opacity: 0;
    transition: opacity $transition-100;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      transform: translateY(-25%);
      width: 100%;
      height: 6px;
      background-color: $blue-1000;
      border-radius: 10px; }
    @include r(1023) {
      display: none;
      &:nth-child(1) {
        &::before {
          transform: translate(0, -25%); } }
      &:nth-child(2) {
        &::before {
          transform: translate(100%, -25%); } }
      &:nth-child(3) {
        &::before {
          transform: translate(200%, -25%); } }
      &:nth-child(4) {
        &::before {
          transform: translate(300%, -25%); } }
      &:nth-child(5) {
        &::before {
          transform: translate(400%, -25%); } }
      &:nth-child(6) {
        &::before {
          transform: translate(500%, -25%); } }
      &:nth-child(7) {
        &::before {
          transform: translate(600%, -25%); } }
      &:nth-child(8) {
        &::before {
          transform: translate(700%, -25%); } } }
    &--active {
      opacity: 1;
      @include r(1023) {
        display: flex; } } }
  &__navigation-list-item-block {
    display: flex;
    align-items: center;
    @include r(1023) {
      width: 100vw; } }
  &__navigation-list-item-icon {
    width: 40px;
    height: 40px;
    margin-right: 5px; }
  &__navigation-list-item-text {
    @include rb;
    color: $blue-500;
    font-size: rem(17);
    white-space: nowrap;
    @include r(1023) {
      font-size: rem(30); } }
  &__slider-slide-media-icon {
