@include font-face('Roboto-Thin', '../fonts/');
@include font-face('Roboto-Light', '../fonts/');
@include font-face('Roboto-Regular', '../fonts/');
@include font-face('Roboto-Medium', '../fonts/');
@include font-face('Roboto-Bold', '../fonts/');
@include font-face('Roboto-Black', '../fonts/');

@mixin rt {
  font-family: 'Roboto-Thin', sans-serif; }

@mixin rl {
  font-family: 'Roboto-Light', sans-serif; }

@mixin rr {
  font-family: 'Roboto-Regular', sans-serif; }

@mixin rm {
  font-family: 'Roboto-Medium', sans-serif; }

@mixin rb {
  font-family: 'Roboto-Bold', sans-serif; }

@mixin rbl {
  font-family: 'Roboto-Black', sans-serif; }

.headline-1 {
  font-size: rem(64);
  @include rb;
  @include r(1919) {
    font-size: rem(54); }
  @include r(1365) {
    font-size: rem(48); }
  @include r(1023) {
    font-size: rem(40); }
  @include r(767) {
    font-size: rem(35); } }

.headline-2 {
  font-size: rem(54);
  @include rb; }

.headline-3 {
  font-size: rem(48);
  @include rb;
  @include r(1023) {
    font-size: rem(35); } }

.paragraph-1 {
  font-size: rem(28);
  @include rb;
  @include r(767) {
    font-size: rem(19); } }

.paragraph-2 {
  font-size: rem(23);
  @include rr; }

.paragraph-3 {
  font-size: rem(17);
  @include rr; }

.font-color-white {
  color: $white; }

.font-color-blue {
  color: $blue-500; }


html {
  font-size: 16px;
  color: $text-black;
  line-height: 1.1; }


h1,h2,h3,h4,h5,h6,p {
  margin: 0;
  padding: 0; }

h1,h2,h3,h4,h5,h6 {}

button, p, a, li {}

h1,
h2,
.h1,
.h2 {}


picture,
img {
  max-width: 100%;
  height: auto;
  display: block; }

.typography {
  p,
  li {}

  img {
    max-width: 100%; }

  h1,h2,h3,h4,h5,h6 {} }

img {
  &.lazy {
    background-image: url(data:image/gif;base64,R0lGODlhCgAIAIABAN3d3f///yH5BAEAAAEALAAAAAAKAAgAAAINjAOnyJv2oJOrVXrzKQA7); }
  &.loaded {
    background-image: none; } }
